import { Fragment, useEffect, useRef, useState } from "react";
import styles from "./Modal.module.css"; // Importing the CSS module

export default function Modal({
  title,
  message,
  dangerOption,
  cancelOption,
  dangerAction,
  cancelAction,
  showModal,
}) {
  const [open, setOpen] = useState(false);
  const cancelButtonRef = useRef(null);

  const handleDanger = () => {
    setOpen(false);
    dangerAction();
  };

  const handleCancel = () => {
    setOpen(false);
    cancelAction();
  };

  useEffect(() => {
    if (showModal) {
      setOpen(true);
    } else {
      setOpen(false);
    }
  }, [showModal]);

  return (
    open && (
      <Fragment>
        <div className={styles.overlay} />

        <div className={styles.modalContainer}>
          <div className={styles.modalContent}>
            <div className={styles.header}>
              <div className={styles.iconContainer}>
                {/* SVG Warning Icon */}
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 24 24"
                  className={styles.icon}
                >
                  <path
                    fillRule="evenodd"
                    d="M11.293 1.293a1 1 0 0 1 1.414 0L22 12l-9.293 10.707a1 1 0 0 1-1.414-1.414L19.586 13H4.414l7.707 8.293a1 1 0 0 1-1.414 1.414L2 12l9.293-10.707a1 1 0 0 1 0-1.414z"
                    clipRule="evenodd"
                  />
                </svg>
              </div>
              <div>
                <h3 className={styles.title}>{title}</h3>
                <p className={styles.message}>{message}</p>
              </div>
            </div>
            <div className={styles.buttonContainer}>
            <button
                type="button"
                className={styles.cancelButton}
                onClick={handleCancel}
                ref={cancelButtonRef}
              >
                {cancelOption}
              </button>
              <button
                type="button"
                className={styles.dangerButton}
                onClick={handleDanger}
              >
                {dangerOption}
              </button>
             
            </div>
          </div>
        </div>
      </Fragment>
    )
  );
}
