import { useDispatch, useSelector } from "react-redux";
import {
  clearSelectedProduct,
  createProductAsync,
  fetchProductByIdAsync,
  selectBrands,
  selectCategories,
  selectProductById,
  updateProductAsync,
} from "../../product/productSlice";
import { useForm } from "react-hook-form";
import { useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import Modal from "../../common/Modal";
import { useAlert } from "react-alert";
import styles from "./ProductForm.module.css";

function ProductForm() {
  const {
    register,
    handleSubmit,
    setValue,
    reset,
    formState: { errors },
  } = useForm();
  const brands = useSelector(selectBrands);
  const categories = useSelector(selectCategories);
  const dispatch = useDispatch();
  const params = useParams();
  const selectedProduct = useSelector(selectProductById);
  const [openModal, setOpenModal] = useState(null);
  const alert = useAlert();
  useEffect(() => {
    if (params.id) {
      dispatch(fetchProductByIdAsync(params.id));
    } else {
      dispatch(clearSelectedProduct());
    }
  }, [params.id, dispatch]);

  useEffect(() => {
    if (selectedProduct && params.id) {
      setValue("title", selectedProduct.title);
      setValue("description", selectedProduct.description);
      setValue("price", selectedProduct.price);
      setValue("discountPercentage", selectedProduct.discountPercentage);
      setValue("thumbnail", selectedProduct.thumbnail);
      setValue("stock", selectedProduct.stock);
      setValue("image1", selectedProduct.images[0]);
      setValue("image2", selectedProduct.images[1]);
      setValue("image3", selectedProduct.images[2]);
      setValue("brand", selectedProduct.brand);
      setValue("category", selectedProduct.category);
    }
  }, [selectedProduct, params.id, setValue]);

  const handleDelete = () => {
    const product = { ...selectedProduct };
    product.deleted = true;
    dispatch(updateProductAsync(product));
  };

  return (
    <div className={styles.mainDiv}>
      <h2>Add Product</h2>
      <form
        noValidate
        onSubmit={handleSubmit((data) => {
          console.log(data);
          const product = { ...data };
          product.images = [
            product.image1,
            product.image2,
            product.image3,
            product.thumbnail,
          ];
          product.rating = 0;
          delete product["image1"];
          delete product["image2"];
          delete product["image3"];
          product.price = +product.price;
          product.stock = +product.stock;
          product.discountPercentage = +product.discountPercentage;
          console.log(product);

          if (params.id) {
            product.id = params.id;
            product.rating = selectedProduct.rating || 0;
            dispatch(updateProductAsync(product));
            alert.success("Product Updated");
            reset();
          } else {
            dispatch(createProductAsync(product));
            alert.success("Product Created");
            reset();
          }
        })}
      >
        <div className={styles.mainSection}>
          {selectedProduct && selectedProduct.deleted && (
            <h2>This product is deleted</h2>
          )}

          <div className={styles.formGroup}>
            <label htmlFor="title" className={styles.label}>
              Product Name
            </label>
            <input
              type="text"
              {...register("title", { required: "name is required" })}
              id="title"
              className={styles.input}
            />
          </div>

          <div className={styles.formGroup}>
            <label htmlFor="description" className={styles.label}>
              Description
            </label>
            <textarea
              id="description"
              {...register("description", {
                required: "description is required",
              })}
              rows={3}
              className={styles.textarea}
              defaultValue={""}
            />
            <p className={styles.helperText}>
              Write a few sentences about the product.
            </p>
          </div>

          <div className={styles.formGroup}>
            <label htmlFor="brand" className={styles.label}>
              Brand
            </label>
            <select
              {...register("brand", { required: "brand is required" })}
              className={styles.select}
            >
              <option value="">--choose brand--</option>
              {brands.map((brand) => (
                <option key={brand.value} value={brand.value}>
                  {brand.label}
                </option>
              ))}
            </select>
          </div>

          <div className={styles.formGroup}>
            <label htmlFor="category" className={styles.label}>
              Category
            </label>
            <select
              {...register("category", { required: "category is required" })}
              className={styles.select}
            >
              <option value="">--choose category--</option>
              {categories.map((category) => (
                <option key={category.value} value={category.value}>
                  {category.label}
                </option>
              ))}
            </select>
          </div>

          <div className={styles.formGroup}>
            <label htmlFor="price" className={styles.label}>
              Price
            </label>
            <input
              type="number"
              {...register("price", {
                required: "price is required",
                min: 1,
                max: 10000,
              })}
              id="price"
              className={styles.input}
            />
          </div>

          <div className={styles.formGroup}>
            <label htmlFor="discountPercentage" className={styles.label}>
              Discount Percentage
            </label>
            <input
              type="number"
              {...register("discountPercentage", {
                required: "discountPercentage is required",
                min: 0,
                max: 100,
              })}
              id="discountPercentage"
              className={styles.input}
            />
          </div>

          <div className={styles.formGroup}>
            <label htmlFor="stock" className={styles.label}>
              Stock
            </label>
            <input
              type="number"
              {...register("stock", { required: "stock is required", min: 0 })}
              id="stock"
              className={styles.input}
            />
          </div>

          <div className={styles.formGroup}>
            <label htmlFor="thumbnail" className={styles.label}>
              Thumbnail
            </label>
            <input
              type="text"
              {...register("thumbnail", { required: "thumbnail is required" })}
              id="thumbnail"
              className={styles.input}
            />
          </div>

          <div className={styles.imgInputGroup}>
            <label htmlFor="image1" className={styles.inputLabel}>
              Image 1
            </label>
            <input
              type="text"
              {...register("image1", { required: "image1 is required" })}
              id="image1"
              className={styles.inputField}
            />
          </div>

          <div className={styles.imgInputGroup}>
            <label htmlFor="image2" className={styles.inputLabel}>
              Image 2
            </label>
            <input
              type="text"
              {...register("image2", { required: "image is required" })}
              id="image2"
              className={styles.inputField}
            />
          </div>

          <div className={styles.imgInputGroup}>
            <label htmlFor="image3" className={styles.inputLabel}>
              Image 3
            </label>
            <input
              type="text"
              {...register("image3", { required: "image is required" })}
              id="image3"
              className={styles.inputField}
            />
          </div>
        </div>

        <div className={styles.section}>
          <h2 className={styles.sectionTitle}>Extra</h2>
          <fieldset className={styles.checkboxGroup}>
            <legend className={styles.legend}>By Email</legend>
            <div className={styles.checkboxItems}>
              <div className={styles.checkboxItem}>
                <input
                  id="comments"
                  name="comments"
                  type="checkbox"
                  className={styles.checkbox}
                />
                <label htmlFor="comments" className={styles.checkboxLabel}>
                  Comments
                </label>
                <p className={styles.checkboxDescription}>
                  Get notified when someone posts a comment on a posting.
                </p>
              </div>

              <div className={styles.checkboxItem}>
                <input
                  id="candidates"
                  name="candidates"
                  type="checkbox"
                  className={styles.checkbox}
                />
                <label htmlFor="candidates" className={styles.checkboxLabel}>
                  Candidates
                </label>
                <p className={styles.checkboxDescription}>
                  Get notified when a candidate applies for a job.
                </p>
              </div>

              <div className={styles.checkboxItem}>
                <input
                  id="offers"
                  name="offers"
                  type="checkbox"
                  className={styles.checkbox}
                />
                <label htmlFor="offers" className={styles.checkboxLabel}>
                  Offers
                </label>
                <p className={styles.checkboxDescription}>
                  Get notified when a candidate accepts or rejects an offer.
                </p>
              </div>
            </div>
          </fieldset>
        </div>

        <div className={styles.buttonGroup}>
          <button type="button" className={styles.cancelButton}>
            Cancel
          </button>

          {selectedProduct && !selectedProduct.deleted && (
            <button
              onClick={(e) => {
                e.preventDefault();
                setOpenModal(true);
              }}
              className={styles.deleteButton}
            >
              Delete
            </button>
          )}

          <button type="submit" className={styles.saveButton}>
            Save
          </button>
        </div>
      </form>

      {selectedProduct && (
        <Modal
          title={`Delete ${selectedProduct.title}`}
          message="Are you sure you want to delete this Product ?"
          dangerOption="Delete"
          cancelOption="Cancel"
          dangerAction={handleDelete}
          cancelAction={() => setOpenModal(null)}
          showModal={openModal}
        ></Modal>
      )}
    </div>
  );
}

export default ProductForm;
