import styles from './Pagination.module.css';
import { ITEMS_PER_PAGE } from "../../app/constants";

export default function Pagination({ page, setPage, handlePage, totalItems }) {
    const totalPages = Math.ceil(totalItems / ITEMS_PER_PAGE);

    return (
      <div className={styles.paginationContainer}>
        <div className={styles.desktopPagination}>
          <div>
            <p className={styles.pageInfo}>
              Showing{' '}
              <span className={styles.pageNumber}>
                {(page - 1) * ITEMS_PER_PAGE + 1}
              </span>{' '}
              to{' '}
              <span className={styles.pageNumber}>
                {page * ITEMS_PER_PAGE > totalItems
                  ? totalItems
                  : page * ITEMS_PER_PAGE}
              </span>{' '}
              of <span className={styles.pageNumber}>{totalItems}</span> results
            </p>
          </div>
          <div>
            <nav className={styles.navContainer} aria-label="Pagination">
              {/* Previous Button */}
              <div
                onClick={(e) => handlePage(page > 1 ? page - 1 : page)}
                className={styles.pageNavButton}
              >
                {/* <span className="sr-only">Previous</span> */}
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className={styles.icon}
                  viewBox="0 0 20 20"
                  fill="currentColor"
                  aria-hidden="true"
                >
                  <path
                    fillRule="evenodd"
                    d="M13 5l-7 7 7 7"
                    clipRule="evenodd"
                  />
                </svg>
              </div>

              {/* Page Number Buttons */}
              {Array.from({ length: totalPages }).map((el, index) => (
                <div
                  key={index}
                  onClick={(e) => handlePage(index + 1)}
                  aria-current="page"
                  className={`${styles.pageNavButton} ${
                    index + 1 === page ? styles.activePage : styles.inactivePage
                  }`}
                >
                  {index + 1}
                </div>
              ))}

              {/* Next Button */}
              <div
                onClick={(e) => handlePage(page < totalPages ? page + 1 : page)}
                className={styles.pageNavButton}
              >
                {/* <span className="sr-only">Next</span> */}
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className={styles.icon}
                  viewBox="0 0 20 20"
                  fill="currentColor"
                  aria-hidden="true"
                >
                  <path
                    fillRule="evenodd"
                    d="M7 5l7 7-7 7"
                    clipRule="evenodd"
                  />
                </svg>
              </div>
            </nav>
          </div>
        </div>
      </div>
    );
}
