import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  fetchBrandsAsync,
  fetchCategoriesAsync,
  fetchProductsByFiltersAsync,
  selectAllProducts,
  selectBrands,
  selectCategories,
  selectProductListStatus,
  selectTotalItems,
} from "../productSlice";
import styles from "./ProductList.module.css"; // Importing the CSS module
import { Link } from "react-router-dom";
import Pagination from "../../common/Pagination";
import { Grid } from "react-loader-spinner";
import { ITEMS_PER_PAGE, discountedPrice } from "../../../app/constants";

function ProductList() {
  const dispatch = useDispatch();
  const products = useSelector(selectAllProducts);
  const brands = useSelector(selectBrands);
  const categories = useSelector(selectCategories);
  const totalItems = useSelector(selectTotalItems);
  const status = useSelector(selectProductListStatus);

  const filters = [
    { id: "category", name: "Category", options: categories },
    { id: "brand", name: "Brands", options: brands },
  ];

  const [filter, setFilter] = useState({});
  const [sort, setSort] = useState({});
  const [isFilterModalOpen, setIsFilterModalOpen] = useState(false);
  const [isSortModalOpen, setIsSortModalOpen] = useState(false);
  const [page, setPage] = useState(1);
  const [activeFilter, setActiveFilter] = useState(null);
  const [selectedSort, setSelectedSort] = useState("");
  const [selectedOrder, setSelectedOrder] = useState("");

  const handleFilter = (e, section, option) => {
    const newFilter = { ...filter };
    if (e.target.checked) {
      newFilter[section.id] = newFilter[section.id]
        ? [...newFilter[section.id], option.value]
        : [option.value];
    } else {
      newFilter[section.id] = newFilter[section.id].filter(
        (el) => el !== option.value
      );
    }
    setFilter(newFilter);
  };

  const handleSort = (option) => {
    setSort({ _sort: option.sort, _order: option.order });
    setSelectedSort(option.sort);
    setSelectedOrder(option.order);
  };

  const handlePage = (page) => {
    setPage(page);
  };

  //  Reset filter state when switching filter type
  const handleFilterChange = (newFilter) => {
    setActiveFilter(newFilter);
    setFilter({}); //  Reset all selected checkboxes
  };

  useEffect(() => {
    const pagination = { _page: page, _limit: ITEMS_PER_PAGE };
    dispatch(fetchProductsByFiltersAsync({ filter, sort, pagination }));
  }, [dispatch, filter, sort, page]);

  useEffect(() => {
    setPage(1);
  }, [totalItems, sort]);

  useEffect(() => {
    dispatch(fetchBrandsAsync());
    dispatch(fetchCategoriesAsync());
  }, []);

  const toggleFilterModal = () => {
    setIsFilterModalOpen(!isFilterModalOpen);
    if (isSortModalOpen) setIsSortModalOpen(false);
  };

  const toggleSortModal = () => {
    setIsSortModalOpen(!isSortModalOpen);
    if (isFilterModalOpen) setIsFilterModalOpen(false);
  };

  return (
    <div className={styles.productListWrapper}>
      <main className={styles.mainContent}>
        <div className={styles.header}>
          <h1>All Products</h1>
          <div className={styles.buttonContainer}>
            <button className={styles.toggleButton} onClick={toggleFilterModal}>
              Filters
            </button>
            <button className={styles.toggleButton} onClick={toggleSortModal}>
              Sort
            </button>
          </div>
        </div>

        <div className={styles.filtersAndProducts}>
          {/* Modal for filters */}
          {isFilterModalOpen && (
            <div className={`${styles.modal} ${styles.open}`}>
              <div className={styles.modalContent}>
                <button
                  className={styles.closeModal}
                  onClick={toggleFilterModal}
                >
                  X
                </button>
                <div className={styles.sort}>
                  <h3>Filter By</h3>
                  <button
                    onClick={() => handleFilterChange("category")}
                    className={
                      activeFilter === "category" ? styles.selected : ""
                    }
                  >
                    Category
                  </button>
                  <button
                    onClick={() => handleFilterChange("brand")}
                    className={activeFilter === "brand" ? styles.selected : ""}
                  >
                    Brand
                  </button>

                  {/* Render filter options dynamically based on active filter */}
                  {activeFilter && (
                    <div className={styles.filterOptions}>
                      {filters
                        .find((filter) => filter.id === activeFilter)
                        .options.map((option, optionIdx) => (
                          <div
                            key={option.value}
                            className={styles.filterOption}
                          >
                            <input
                              id={`filter-${activeFilter}-${optionIdx}`}
                              type="checkbox"
                              checked={
                                filter[activeFilter]?.includes(option.value) ||
                                false
                              }
                              onChange={(e) =>
                                handleFilter(
                                  e,
                                  filters.find(
                                    (filter) => filter.id === activeFilter
                                  ),
                                  option
                                )
                              }
                            />
                            <label
                              htmlFor={`filter-${activeFilter}-${optionIdx}`}
                            >
                              {option.label}
                            </label>
                          </div>
                        ))}
                    </div>
                  )}
                </div>
              </div>
            </div>
          )}

          {/* Modal for sorting */}
          {isSortModalOpen && (
            <div className={`${styles.modal} ${styles.open}`}>
              <div className={styles.modalContent}>
                <button className={styles.closeModal} onClick={toggleSortModal}>
                  X
                </button>
                <div className={styles.sort}>
                  <h3>Sort By</h3>
                  <button
                    onClick={() =>
                      handleSort({ sort: "rating", order: "desc" })
                    }
                    className={selectedSort === "rating" ? styles.selected : ""}
                  >
                    Best Rating
                  </button>
                  <button
                    onClick={() => handleSort({ sort: "price", order: "asc" })}
                    className={
                      selectedSort === "price" && selectedOrder === "asc"
                        ? styles.selected
                        : ""
                    }
                  >
                    Price: Low to High
                  </button>
                  <button
                    onClick={() => handleSort({ sort: "price", order: "desc" })}
                    className={
                      selectedSort === "price" && selectedOrder === "desc"
                        ? styles.selected
                        : ""
                    }
                  >
                    Price: High to Low
                  </button>
                </div>
              </div>
            </div>
          )}

          {/* Product grid */}
          <div className={styles.productGrid}>
            {status === "loading" ? (
              <Grid
                height="80"
                width="80"
                color="rgb(79, 70, 229)"
                ariaLabel="grid-loading"
                visible={true}
              />
            ) : (
              products.map((product) => (
                <Link to={`/product-detail/${product.id}`} key={product.id} className={styles.productLink}>
                  <div className={styles.productCard}>
                    <img
                      src={product.thumbnail}
                      alt={product.title}
                      className={styles.productImage}
                    />
                    <div className={styles.productInfo}>
                      <h3>{product.title}</h3>
                      <p>{product.rating}</p>
                      <p>${discountedPrice(product)}</p>
                      <p className={styles.strikeThrough}>${product.price}</p>
                    </div>
                  </div>
                </Link>
              ))
            )}
          </div>
        </div>

        <Pagination
          page={page}
          setPage={setPage}
          handlePage={handlePage}
          totalItems={totalItems}
        />
      </main>
    </div>
  );
}

export default ProductList;
