import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  fetchBrandsAsync,
  fetchCategoriesAsync,
  fetchProductsByFiltersAsync,
  selectAllProducts,
  selectBrands,
  selectCategories,
  selectTotalItems,
} from "../../product/productSlice";
import { Link } from "react-router-dom";
import { ITEMS_PER_PAGE, discountedPrice } from "../../../app/constants";
import styles from './AdminProductList.module.css';
import Pagination from "../../common/Pagination";

const sortOptions = [
  { name: "Best Rating", sort: "rating", order: "desc", current: false },
  { name: "Price: Low to High", sort: "price", order: "asc", current: false },
  { name: "Price: High to Low", sort: "price", order: "desc", current: false },
];

function AdminProductList() {
  const dispatch = useDispatch();
  const products = useSelector(selectAllProducts);
  const brands = useSelector(selectBrands);
  const categories = useSelector(selectCategories);
  const totalItems = useSelector(selectTotalItems);

  const [filter, setFilter] = useState({});
  const [sort, setSort] = useState({});
  const [page, setPage] = useState(1);

  const filters = [
    { id: "category", name: "Category", options: categories },
    { id: "brand", name: "Brands", options: brands },
  ];

  const handleFilter = (e, section, option) => {
    const newFilter = { ...filter };
    if (e.target.checked) {
      if (newFilter[section.id]) {
        newFilter[section.id].push(option.value);
      } else {
        newFilter[section.id] = [option.value];
      }
    } else {
      const index = newFilter[section.id].findIndex(el => el === option.value);
      newFilter[section.id].splice(index, 1);
    }
    setFilter(newFilter);
  };

  const handleSort = (e, option) => {
    setSort({ _sort: option.sort, _order: option.order });
  };

  const handlePage = (page) => setPage(page);

  useEffect(() => {
    const pagination = { _page: page, _limit: ITEMS_PER_PAGE };
    dispatch(fetchProductsByFiltersAsync({ filter, sort, pagination, admin: true }));
  }, [dispatch, filter, sort, page]);

  useEffect(() => {
    setPage(1);
  }, [totalItems, sort]);

  useEffect(() => {
    dispatch(fetchBrandsAsync());
    dispatch(fetchCategoriesAsync());
  }, [dispatch]);

  return (
    <div className={styles.container}>
      <div>
        <main className={styles.main}>
          <div className={styles.header}>
            <h1 className={styles.title}>All Products</h1>

            <div className={styles.sortButton}>
              <button onClick={() => setSort(prev => ({ ...prev, _order: 'desc' }))}>
                Sort
              </button>
            </div>
          </div>

          <section className={styles.productSection}>
            <div className={styles.grid}>
              <DesktopFilter handleFilter={handleFilter} filters={filters} />
              <div className={styles.productGrid}>
                <Link to="/admin/product-form" className={styles.addProductButton}>
                  Add New Product
                </Link>
                <ProductGrid products={products} />
              </div>
            </div>
          </section>

          <Pagination
            page={page}
            setPage={setPage}
            handlePage={handlePage}
            totalItems={totalItems}
          />
        </main>
      </div>
    </div>
  );
}

function DesktopFilter({ handleFilter, filters }) {
  return (
    <form className={styles.filterForm}>
      {filters.map((section) => (
        <div key={section.id} className={styles.filterSection}>
          <h3>{section.name}</h3>
          <div className={styles.filterOptions}>
            {section.options.map((option, optionIdx) => (
              <div key={option.value} className={styles.filterOption}>
                <input
                  id={`filter-${section.id}-${optionIdx}`}
                  name={`${section.id}[]`}
                  value={option.value}
                  type="checkbox"
                  onChange={(e) => handleFilter(e, section, option)}
                  className={styles.checkbox}
                />
                <label htmlFor={`filter-${section.id}-${optionIdx}`} className={styles.label}>
                  {option.label}
                </label>
              </div>
            ))}
          </div>
        </div>
      ))}
    </form>
  );
}

function ProductGrid({ products }) {
  return (
    <div className={styles.productContainer}>
      <div className={styles.productList}>
        {products.map((product) => (
          <div key={product.id} className={styles.productCard}>
            <Link to={`/product-detail/${product.id}`} className={styles.productLink}>
              <div className={styles.productImage}>
                <img src={product.thumbnail} alt={product.title} />
              </div>
              <div className={styles.productInfo}>
                <h3>{product.title}</h3>
                <div className={styles.productRating}>
                  <span>{product.rating}</span>
                </div>
                <div className={styles.productPrice}>
                  <p className={styles.discountedPrice}>${discountedPrice(product)}</p>
                  <p className={styles.originalPrice}>${product.price}</p>
                </div>
              </div>
            </Link>
            <div className={styles.editButtonContainer}>
              <Link to={`/admin/product-form/edit/${product.id}`} className={styles.editButton}>
                Edit Product
              </Link>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default AdminProductList;
