import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  fetchProductByIdAsync,
  selectProductById,
  selectProductListStatus,
} from "../productSlice";
import { useParams } from "react-router-dom";
import { addToCartAsync, selectItems } from "../../cart/cartSlice";
import { selectLoggedInUser } from "../../auth/authSlice";
import { discountedPrice } from "../../../app/constants";
import { useAlert } from "react-alert";
import styles from "./ProductDetail.module.css";

export default function ProductDetail() {
  const [selectedColor, setSelectedColor] = useState("White");
  const [selectedSize, setSelectedSize] = useState("M");
  const [expandedImage, setExpandedImage] = useState(null);
  const user = useSelector(selectLoggedInUser);
  const items = useSelector(selectItems);
  const product = useSelector(selectProductById);
  const dispatch = useDispatch();
  const params = useParams();
  const alert = useAlert();
  const status = useSelector(selectProductListStatus);

  const handleCart = (e) => {
    e.preventDefault();
    if (items.findIndex((item) => item.product.id === product.id) < 0) {
      const newItem = {
        product: product.id,
        quantity: 1,
        user: user.id,
      };
      dispatch(addToCartAsync(newItem));
      alert.success("Item added to Cart");
    } else {
      alert.error("Item Already added");
    }
  };

  const handleImageClick = (image) => {
    setExpandedImage(image);
  };

  const closeExpandedImage = () => {
    setExpandedImage(null);
  };

  useEffect(() => {
    dispatch(fetchProductByIdAsync(params.id));
  }, [dispatch, params.id]);

  return (
    <div className={styles.container}>
      {status === "loading" && <div className={styles.loading}>Loading...</div>}
      {product && (
        <div className={styles.productDetail}>
          <div className={styles.productImages}>
            <img
              src={product.images[0]}
              alt={product.title}
              className={styles.mainImage}
              onClick={() => handleImageClick(product.images[0])}
            />
            <div className={styles.imageGrid}>
              {product.images.slice(1).map((image, index) => (
                <img
                  key={index}
                  src={image}
                  alt={product.title}
                  className={styles.gridImage}
                  onClick={() => handleImageClick(image)}
                />
              ))}
            </div>
          </div>

          {expandedImage && (
            <div
              className={styles.expandedImageOverlay}
              onClick={closeExpandedImage}
            >
              <img
                src={expandedImage}
                alt="Expanded View"
                className={styles.expandedImage}
              />
            </div>
          )}

          <div className={styles.productInfo}>
            <div className={styles.section1}>
              <h1 className={styles.title}>{product.title}</h1>
              <p className={styles.oldPrice}>${product.price}</p>
              <p className={styles.newPrice}>${discountedPrice(product)}</p>
            </div>

            <form onSubmit={handleCart}>
              <button type="submit" className={styles.addButton}>
                Add to Cart
              </button>

              {/* Modern Color Selection */}
              <div className={styles.optionGroup}>
                <label>Color</label>
                <div className={styles.colorOptions}>
                  {["White", "Gray", "Black"].map((color) => (
                    <button
                      key={color}
                      type="button"
                      className={`${styles.colorButton} ${
                        selectedColor === color ? styles.selectedColor : ""
                      }`}
                      style={{ backgroundColor: color.toLowerCase() }}
                      onClick={() => setSelectedColor(color)}
                    />
                  ))}
                </div>
              </div>

              {/* Modern Size Selection */}
              <div className={styles.optionGroup}>
                <label>Size</label>
                <div className={styles.sizeOptions}>
                  {["XS", "S", "M", "L", "XL"].map((size) => (
                    <button
                      key={size}
                      type="button"
                      className={`${styles.sizeButton} ${
                        selectedSize === size ? styles.selectedSize : ""
                      }`}
                      onClick={() => setSelectedSize(size)}
                    >
                      {size}
                    </button>
                  ))}
                </div>
              </div>
            </form>
            <p className={styles.description}>{product.description}</p>
          </div>
        </div>
      )}
    </div>
  );
}
