import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import {
  deleteItemFromCartAsync,
  selectItems,
  updateCartAsync,
} from "../features/cart/cartSlice";
import { Navigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import { updateUserAsync } from "../features/user/userSlice";
import { useState } from "react";
import {
  createOrderAsync,
  selectCurrentOrder,
} from "../features/order/orderSlice";
import { selectUserInfo } from "../features/user/userSlice";
import { discountedPrice } from "../app/constants";
import styles from "./Checkout.module.css";

function Checkout() {
  const dispatch = useDispatch();
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();

  const user = useSelector(selectUserInfo);
  const items = useSelector(selectItems);
  const currentOrder = useSelector(selectCurrentOrder);

  const totalAmount = items.reduce(
    (amount, item) => discountedPrice(item.product) * item.quantity + amount,
    0
  );
  const totalItems = items.reduce((total, item) => item.quantity + total, 0);

  const [selectedAddress, setSelectedAddress] = useState(null);
  const [paymentMethod, setPaymentMethod] = useState(null);

  const handleQuantity = (e, item) => {
    dispatch(updateCartAsync({ id: item.id, quantity: +e.target.value }));
  };

  const handleRemove = (e, id) => {
    dispatch(deleteItemFromCartAsync(id));
  };

  const handleAddress = (e) => {
    console.log(e.target.value);
    setSelectedAddress(user.addresses[e.target.value]);
  };

  const handlePayment = (e) => {
    console.log(e.target.value);
    setPaymentMethod(e.target.value);
  };

  const handleOrder = (e) => {
    if (selectedAddress && paymentMethod) {
      const order = {
        items,
        totalAmount,
        totalItems,
        user: user.id,
        paymentMethod,
        selectedAddress,
        status: "pending", // other status can be delivered, received.
      };
      dispatch(createOrderAsync(order));
      // need to redirect from here to a new page of order success.
    } else {
      // TODO : we can use proper messaging popup here
      alert("Enter Address and Payment method");
    }
    //TODO : Redirect to order-success page
    //TODO : clear cart after order
    //TODO : on server change the stock number of items
  };

  return (
    <>
      {!items.length && <Navigate to="/" replace={true}></Navigate>}
      {currentOrder && (
        <Navigate
          to={`/order-success/${currentOrder.id}`}
          replace={true}
        ></Navigate>
      )}

      <div className={styles.main}>
        <div className={styles.section1}>
          {/* This form is for address */}
          <form
            className={styles.formContainer}
            noValidate
            onSubmit={handleSubmit((data) => {
              console.log(data);
              dispatch(
                updateUserAsync({
                  ...user,
                  addresses: [...user.addresses, data],
                })
              );
              reset();
            })}
          >
            <div className={styles.formWrapper}>
              <div className={styles.section}>
                <h2 className={styles.heading}>Personal Information</h2>
                <p className={styles.subText}>
                  Use a permanent address where you can receive mail.
                </p>

                <div className={styles.gridContainer}>
                  <div className={styles.inputGroup}>
                    <label htmlFor="name">Full name</label>
                    <input
                      type="text"
                      {...register("name", { required: "name is required" })}
                      id="name"
                    />
                    {errors.name && (
                      <p className={styles.errorText}>{errors.name.message}</p>
                    )}
                  </div>

                  <div className={styles.inputGroup}>
                    <label htmlFor="email">Email address</label>
                    <input
                      type="email"
                      {...register("email", {
                        required: "email is required",
                      })}
                      id="email"
                    />
                    {errors.email && (
                      <p className={styles.errorText}>{errors.email.message}</p>
                    )}
                  </div>

                  <div className={styles.inputGroup}>
                    <label htmlFor="phone">Phone</label>
                    <input
                      type="tel"
                      {...register("phone", {
                        required: "phone is required",
                      })}
                      id="phone"
                    />
                    {errors.phone && (
                      <p className={styles.errorText}>{errors.phone.message}</p>
                    )}
                  </div>

                  <div className={styles.inputGroup}>
                    <label htmlFor="street">Street address</label>
                    <input
                      type="text"
                      {...register("street", {
                        required: "street is required",
                      })}
                      id="street"
                    />
                    {errors.street && (
                      <p className={styles.errorText}>
                        {errors.street.message}
                      </p>
                    )}
                  </div>

                  <div className={styles.inputGroup}>
                    <label htmlFor="city">City</label>
                    <input
                      type="text"
                      {...register("city", { required: "city is required" })}
                      id="city"
                    />
                    {errors.city && (
                      <p className={styles.errorText}>{errors.city.message}</p>
                    )}
                  </div>

                  <div className={styles.inputGroup}>
                    <label htmlFor="state">State / Province</label>
                    <input
                      type="text"
                      {...register("state", {
                        required: "state is required",
                      })}
                      id="state"
                    />
                    {errors.state && (
                      <p className={styles.errorText}>{errors.state.message}</p>
                    )}
                  </div>

                  <div className={styles.inputGroup}>
                    <label htmlFor="pinCode">ZIP / Postal code</label>
                    <input
                      type="text"
                      {...register("pinCode", {
                        required: "pinCode is required",
                      })}
                      id="pinCode"
                    />
                    {errors.pinCode && (
                      <p className={styles.errorText}>
                        {errors.pinCode.message}
                      </p>
                    )}
                  </div>
                </div>
              </div>

              <div className={styles.buttonGroup}>
                <button
                  type="button"
                  className={styles.resetButton}
                  onClick={() => reset()}
                >
                  Reset
                </button>
                <button type="submit" className={styles.submitButton}>
                  Add Address
                </button>
              </div>
            </div>
          </form>

          {/* This section  is for payment methods */}
          <div className={styles.container}>
            <h2 className={styles.heading}>Addresses</h2>
            <p className={styles.subText}>Choose from Existing addresses</p>
            <ul>
              {user.addresses.map((address, index) => (
                <li key={index} className={styles.addressItem}>
                  <div className={styles.addressWrapper}>
                    <input
                      onChange={handleAddress}
                      name="address"
                      type="radio"
                      value={index}
                      className={styles.radioButton}
                    />
                    <div className={styles.addressDetails}>
                      <p className={styles.name}>{address.name}</p>
                      <p className={styles.text}>{address.street}</p>
                      <p className={styles.text}>{address.pinCode}</p>
                    </div>
                  </div>
                  <div className={styles.addressRight}>
                    <p className={styles.phone}>Phone: {address.phone}</p>
                    <p className={styles.text}>{address.city}</p>
                  </div>
                </li>
              ))}
            </ul>

            <div className={styles.paymentSection}>
              <fieldset>
                <legend className={styles.paymentHeading}>
                  Payment Methods
                </legend>
                <p className={styles.subText}>Choose One</p>
                <div className={styles.paymentOptions}>
                  <div className={styles.optionWrapper}>
                    <input
                      id="cash"
                      name="payments"
                      onChange={handlePayment}
                      value="cash"
                      type="radio"
                      checked={paymentMethod === "cash"}
                      className={styles.radioButton}
                    />
                    <label htmlFor="cash" className={styles.optionLabel}>
                      Cash
                    </label>
                  </div>
                  <div className={styles.optionWrapper}>
                    <input
                      id="card"
                      onChange={handlePayment}
                      name="payments"
                      checked={paymentMethod === "card"}
                      value="card"
                      type="radio"
                      className={styles.radioButton}
                    />
                    <label htmlFor="card" className={styles.optionLabel}>
                      Card Payment
                    </label>
                  </div>
                </div>
              </fieldset>
            </div>
          </div>
        </div>

        <div className={styles.section2}>
          {/* This section is for cart */}
          <div className={styles.cartContainer}>
            <h1 className={styles.cartTitle}>Cart</h1>
            <div className={styles.flowRoot}>
              <ul className={styles.itemList}>
                {items.map((item) => (
                  <li key={item.id} className={styles.item}>
                    <div className={styles.imageContainer}>
                      <img
                        src={item.product.thumbnail}
                        alt={item.product.title}
                        className={styles.image}
                      />
                    </div>

                    <div className={styles.itemDetails}>
                      <div>
                        <div className={styles.itemHeader}>
                          <h3>
                            <a href={item.product.id}>{item.product.title}</a>
                          </h3>
                          <p className={styles.price}>
                            ${discountedPrice(item.product)}
                          </p>
                        </div>
                        <p className={styles.brand}>{item.product.brand}</p>
                      </div>
                      <div className={styles.itemFooter}>
                        <div className={styles.quantity}>
                          <label
                            htmlFor="quantity"
                            className={styles.quantityLabel}
                          >
                            Qty
                          </label>
                          <select
                            onChange={(e) => handleQuantity(e, item)}
                            value={item.quantity}
                          >
                            <option value="1">1</option>
                            <option value="2">2</option>
                            <option value="3">3</option>
                            <option value="4">4</option>
                            <option value="5">5</option>
                          </select>
                        </div>
                        <div className={styles.removeButton}>
                          <button
                            onClick={(e) => handleRemove(e, item.id)}
                            type="button"
                          >
                            Remove
                          </button>
                        </div>
                      </div>
                    </div>
                  </li>
                ))}
              </ul>
            </div>
          </div>

          {/* This section is for summary */}
          <div className={styles.summaryContainer}>
            <div className={styles.row}>
              <p>Subtotal</p>
              <p>${totalAmount}</p>
            </div>
            <div className={styles.row}>
              <p>Total Items in Cart</p>
              <p>{totalItems} items</p>
            </div>
            <p className={styles.shippingText}>
              Shipping and taxes calculated at checkout.
            </p>
            <div className={styles.orderButtonContainer}>
              <div onClick={handleOrder} className={styles.orderButton}>
                Order Now
              </div>
            </div>
            <div className={styles.continueShopping}>
              <p>
                or
                <Link to="/">
                  <button type="button" className={styles.continueButton}>
                    Continue Shopping <span aria-hidden="true"> &rarr;</span>
                  </button>
                </Link>
              </p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Checkout;
