import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { selectUserInfo, updateUserAsync } from "../userSlice";
import { useForm } from "react-hook-form";
import styles from "./UserProfile.module.css";

export default function UserProfile() {
  const dispatch = useDispatch();
  const userInfo = useSelector(selectUserInfo);
  const [selectedEditIndex, setSelectedEditIndex] = useState(-1);
  const [showAddAddressForm, setShowAddAddressForm] = useState(false);

  const {
    register,
    handleSubmit,
    reset,
    setValue,
    formState: { errors },
  } = useForm();

  const handleEdit = (addressUpdate, index) => {
    const newUser = { ...userInfo, addresses: [...userInfo.addresses] };
    newUser.addresses.splice(index, 1, addressUpdate);
    dispatch(updateUserAsync(newUser));
    setSelectedEditIndex(-1);
  };

  const handleRemove = (e, index) => {
    const newUser = { ...userInfo, addresses: [...userInfo.addresses] };
    newUser.addresses.splice(index, 1);
    dispatch(updateUserAsync(newUser));
  };

  const handleEditForm = (index) => {
    setSelectedEditIndex(index);
    const address = userInfo.addresses[index];
    setValue("name", address.name);
    setValue("email", address.email);
    setValue("city", address.city);
    setValue("state", address.state);
    setValue("pinCode", address.pinCode);
    setValue("phone", address.phone);
    setValue("street", address.street);
  };

  const handleAdd = (address) => {
    const newUser = {
      ...userInfo,
      addresses: [...userInfo.addresses, address],
    };
    dispatch(updateUserAsync(newUser));
    setShowAddAddressForm(false);
  };

  return (
    <div className={styles.container}>
      <div className={styles.userInfo}>
        <h1 className={styles.title}>Name: {userInfo.name || "New User"}</h1>
        <h3 className={styles.subtitle}>Email: {userInfo.email}</h3>
        {userInfo.role === "admin" && (
          <h3 className={styles.subtitle}>Role: {userInfo.role}</h3>
        )}
      </div>

      <div className={styles.addressSection}>
        <button
          onClick={() => {
            setShowAddAddressForm(true);
            setSelectedEditIndex(-1);
          }}
          className={styles.button}
        >
          Add New Address
        </button>

        {showAddAddressForm && (
          <form
            className={styles.form}
            noValidate
            onSubmit={handleSubmit((data) => {
              handleAdd(data);
              reset();
            })}
          >
            <div className={styles.formTitle}>Personal Information</div>
            <p className={styles.formDescription}>
              Use a permanent address where you can receive mail.
            </p>

            <div className={styles.formGrid}>
              {[
                "name",
                "email",
                "phone",
                "street",
                "city",
                "state",
                "pinCode",
              ].map((field) => (
                <div key={field} className={styles.formField}>
                  <label htmlFor={field} className={styles.label}>
                    {field.charAt(0).toUpperCase() + field.slice(1)}
                  </label>
                  <input
                    {...register(field, { required: `${field} is required` })}
                    id={field}
                    className={styles.input}
                  />
                  {errors[field] && (
                    <p className={styles.error}>{errors[field].message}</p>
                  )}
                </div>
              ))}
            </div>

            <button type="submit" className={styles.submitButton}>
              Add Address
            </button>
          </form>
        )}

        <div className={styles.addressList}>
          <p>Your Addresses:</p>
          {userInfo.addresses.map((address, index) => (
            <div key={index} className={styles.addressContainer}>
              {selectedEditIndex === index ? (
                <form
                  className={styles.form}
                  noValidate
                  onSubmit={handleSubmit((data) => {
                    handleEdit(data, index);
                    reset();
                  })}
                >
                  <div className={styles.formTitle}>Edit Address</div>
                  <div className={styles.formGrid}>
                    {[
                      "name",
                      "email",
                      "phone",
                      "street",
                      "city",
                      "state",
                      "pinCode",
                    ].map((field) => (
                      <div key={field} className={styles.formField}>
                        <label htmlFor={field} className={styles.label}>
                          {field.charAt(0).toUpperCase() + field.slice(1)}
                        </label>
                        <input
                          {...register(field, {
                            required: `${field} is required`,
                          })}
                          id={field}
                          defaultValue={address[field]}
                          className={styles.input}
                        />
                        {errors[field] && (
                          <p className={styles.error}>
                            {errors[field].message}
                          </p>
                        )}
                      </div>
                    ))}
                  </div>

                  <div className={styles.buttonContainer}>
                    <button
                      type="button"
                      className={styles.cancelButton}
                      onClick={() => setSelectedEditIndex(-1)}
                    >
                      Cancel
                    </button>
                    <button type="submit" className={styles.submitButton}>
                      Save Changes
                    </button>
                  </div>
                </form>
              ) : (
                <div className={styles.addressDetails}>
                  <p>{address.name}</p>
                  <p>{address.street}</p>
                  <p>
                    {address.city}, {address.state} {address.pinCode}
                  </p>
                  <p>Phone: {address.phone}</p>
                  <div className={styles.addressButtons}>
                    <button
                      onClick={() => handleEditForm(index)}
                      className={styles.editButton}
                    >
                      Edit
                    </button>
                    <button
                      onClick={(e) => handleRemove(e, index)}
                      className={styles.removeButton}
                    >
                      Remove
                    </button>
                  </div>
                </div>
              )}
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
