import { useState, useEffect } from "react";
import { Link, NavLink } from "react-router-dom";
import { useSelector } from "react-redux";
import { selectItems } from "../cart/cartSlice";
import { selectLoggedInUser } from "../auth/authSlice";
import styles from "./Navbar.module.css";

function Navbar({ children }) {
  const items = useSelector(selectItems);
  const user = useSelector(selectLoggedInUser);
  const [menuOpen, setMenuOpen] = useState(false);
  const [userMenuOpen, setUserMenuOpen] = useState(false); // Separate state for user menu
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768);
    };

    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  const navigation = [
    { name: "Products", link: "/", user: true },
    { name: "Products", link: "/admin", admin: true },
    { name: "Orders", link: "/admin/orders", admin: true },
  ];

  const userNavigation = [
    { name: "My Profile", link: "/profile" },
    { name: "My Orders", link: "/orders" },
    { name: "Sign out", link: "/logout" },
  ];

  function toggleMenu(e) {
    e.stopPropagation();
    setMenuOpen(!menuOpen);
  }
  function toggleUserMenu(e) {
    e.stopPropagation();
    setUserMenuOpen(!userMenuOpen); // Toggle for user menu only
  }

  // Desktop Navbar
  const DesktopNavbar = () => (
    <div className={styles.mainDiv}>
      <nav className={styles.navbar}>
        <div className={styles.logo}>
          <Link to="/" className={styles.navlinks}>
            E-Commerce
          </Link>
        </div>

        <div>
          <ul className={styles.navList}>
            {navigation.map(
              (item) =>
                item[user.role] && (
                  <li key={item.name}>
                    <Link
                      to={item.link}
                      className={({ isActive }) =>
                        `${styles.navlinks} ${isActive ? styles.active : ""}`
                      }
                    >
                      {item.name}
                    </Link>
                  </li>
                )
            )}
          </ul>
        </div>

        <div className={styles.cartAndUser}>
          <div className={styles.cart}>
            <Link to="/cart" className={styles.cartIcon}>
              <div className={styles.cartAndUser}>
                {" "}
                
                {items.length > 0 && (
                  <span className={styles.cartBadge}>{items.length}</span>
                )}
              </div>
            </Link>
          </div>

          <div className={styles.user}>
            <button onClick={toggleUserMenu} className={styles.userButton}>
              
            </button>
            {userMenuOpen && (
              <div className={styles.dropdownMenu}>
                {userNavigation.map((item) => (
                  <Link
                    key={item.name}
                    to={item.link}
                    className={styles.dropdownItem}
                  >
                    {item.name}
                  </Link>
                ))}
              </div>
            )}
          </div>
      
        </div>
      </nav>
    </div>
  );

  // Mobile Navbar
  const MobileNavbar = () => (
    <div className={styles.mainDiv}>
      <nav className={styles.navbar}>
        <div className={styles.logo}>
          <NavLink to="/" className={styles.navlinks}>
            <span className={styles.webName}>E-Commerce</span>
          </NavLink>
        </div>
        <button
          className={styles.hamburger}
          onClick={toggleMenu}
          aria-label="Toggle Navigation"
        >
          
        </button>
        <ul className={`${styles.navList} ${menuOpen ? styles.showMenu : ""}`}>
          {navigation.map(
            (item) =>
              item[user.role] && (
                <li key={item.name}>
                  <Link
                    to={item.link}
                    className={({ isActive }) =>
                      `${styles.navlinks} ${isActive ? styles.active : ""}`
                    }
                    onClick={() => setMenuOpen(false)} // Close menu when link is clicked
                  >
                    {item.name}
                  </Link>
                </li>
              )
          )}
        </ul>
      </nav>

      <div className={styles.cartAndUser}>
        <div className={styles.cart}>
          <Link to="/cart" className={styles.cartIcon}>
            
          </Link>
          {items.length > 0 && (
            <span className={styles.cartBadge}>{items.length}</span>
          )}
        </div>

        <div className={styles.user}>
          <button onClick={toggleUserMenu} className={styles.userButton}>
            
          </button>
          {userMenuOpen && (
            <div className={styles.dropdownMenu}>
              {userNavigation.map((item) => (
                <Link
                  key={item.name}
                  to={item.link}
                  className={styles.dropdownItem}
                  onClick={() => setUserMenuOpen(false)} // Close user menu when link is clicked
                >
                  {item.name}
                </Link>
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );

  return (
    <div className={styles.homeContainer}>
      {isMobile ? <MobileNavbar /> : <DesktopNavbar />}
      <main>{children}</main>
    </div>
  );
}

export default Navbar;
