import { useSelector, useDispatch } from 'react-redux';
import { useForm } from 'react-hook-form';
import { selectLoggedInUser, createUserAsync } from '../authSlice';
import { Link, Navigate } from 'react-router-dom';
import styles from './Signup.module.css';

export default function Signup() {
  const dispatch = useDispatch();
  const user = useSelector(selectLoggedInUser);

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  return (
    <>
      {user && <Navigate to="/" replace={true} />}
      <div className={styles.container}>
        <div className={styles.innerContainer}>
          {/* <img className={styles.logo} src="/ecommerce.png" alt="Your Company" /> */}
          <h2 className={styles.title}>Create a New Account</h2>
        </div>

        <div className={styles.innerContainer}>
          <form
            noValidate
            className={styles.form}
            onSubmit={handleSubmit((data) => {
              dispatch(
                createUserAsync({
                  email: data.email,
                  password: data.password,
                  addresses: [],
                  role: 'user',
                })
              );
              console.log(data);
            })}
          >
            <div>
              <label htmlFor="email" className={styles.label}>Email address</label>
              <input
                id="email"
                {...register('email', {
                  required: 'Email is required',
                  pattern: {
                    value: /\b[\w\.-]+@[\w\.-]+\.\w{2,4}\b/gi,
                    message: 'Email not valid',
                  },
                })}
                type="email"
                className={styles.input}
              />
              {errors.email && <p className={styles.error}>{errors.email.message}</p>}
            </div>

            <div>
              <label htmlFor="password" className={styles.label}>Password</label>
              <input
                id="password"
                {...register('password', {
                  required: 'Password is required',
                  pattern: {
                    value: /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$/gm,
                    message: `- At least 8 characters\n- Must contain at least 1 uppercase letter, 1 lowercase letter, and 1 number\n- Can contain special characters`,
                  },
                })}
                type="password"
                className={styles.input}
              />
              {errors.password && <p className={styles.error}>{errors.password.message}</p>}
            </div>

            <div>
              <label htmlFor="confirmPassword" className={styles.label}>Confirm Password</label>
              <input
                id="confirmPassword"
                {...register('confirmPassword', {
                  required: 'Confirm password is required',
                  validate: (value, formValues) =>
                    value === formValues.password || 'Passwords do not match',
                })}
                type="password"
                className={styles.input}
              />
              {errors.confirmPassword && <p className={styles.error}>{errors.confirmPassword.message}</p>}
            </div>

            <button type="submit" className={styles.button}>Sign Up</button>
          </form>

          <p className={styles.linkContainer}>
            Already a Member?{' '}
            <Link to="/login" className={styles.link}>Log In</Link>
          </p>
        </div>
      </div>
    </>
  );
}
