import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  fetchLoggedInUserOrderAsync,
  selectUserInfo,
  selectUserOrders,
} from "../userSlice";
import { discountedPrice } from "../../../app/constants";
import styles from './UserOrders.module.css'; // Import CSS module

export default function UserOrders() {
  const dispatch = useDispatch();
  const userInfo = useSelector(selectUserInfo);
  const orders = useSelector(selectUserOrders);

  useEffect(() => {
    dispatch(fetchLoggedInUserOrderAsync(userInfo.id));
  }, [dispatch, userInfo]);

  return (
    <div className={styles.container}>
      {orders.map((order) => (
        <div key={order.id} className={styles.orderCard}>
          <h1 className={styles.heading}>Order # {order.id}</h1>
          <h3 className={styles.subheading}>Order Status : {order.status}</h3>
          <div className={styles.listContainer}>
            {order.items.map((item) => (
              <div key={item.id} className={styles.listItem}>
                <div className={styles.imageWrapper}>
                  <img
                    src={item.product.thumbnail}
                    alt={item.product.title}
                    className={styles.image}
                  />
                </div>
                <div className={styles.itemInfo}>
                  <div>
                    <div className={styles.itemTitle}>
                      <a href={item.product.id}>
                        {item.product.title}
                      </a>
                    </div>
                    <div className={styles.itemPrice}>
                      ${discountedPrice(item.product)}
                    </div>
                    <div className={styles.itemBrand}>
                      {item.product.brand}
                    </div>
                  </div>
                  <div className={styles.itemDetails}>
                    <div className={styles.quantityLabel}>
                      Qty: {item.quantity}
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>

          <div className={styles.orderSummary}>
            <div className={styles.summaryRow}>
              <p>Subtotal</p>
              <p>${order.totalAmount}</p>
            </div>
            <div className={styles.summaryRow}>
              <p>Total Items in Cart</p>
              <p>{order.totalItems} items</p>
            </div>
            <p>Shipping Address:</p>
            <div className={styles.shippingAddressContainer}>
              <div className={styles.addressDetails}>
                <div className={styles.addressInfo}>
                  <p className="text-sm font-semibold leading-6 text-gray-900">
                    {order.selectedAddress.name}
                  </p>
                  <p>{order.selectedAddress.street}</p>
                  <p>{order.selectedAddress.pinCode}</p>
                </div>
                <div className={styles.phoneText}>
                  Phone: {order.selectedAddress.phone}
                </div>
                <div className={styles.cityText}>
                  {order.selectedAddress.city}
                </div>
              </div>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}
